<template>
  <div id="main">
    <router-view></router-view>
    <div class="weui_dialog_alert" v-show="showAlert">
      <div class="weui_mask"></div>
      <div class="weui_dialog">
        <div class="weui_dialog_hd"><strong class="weui_dialog_title">{{error.title}}</strong></div>
        <div class="weui_dialog_bd">{{error.msg}}</div>
        <div class="weui_dialog_ft">
          <a @click="showAlert = false" class="weui_btn_dialog primary"></a>
        </div>
      </div>
    </div>
    <div id="toast" v-show="showTip">
      <div class="weui_mask_transparent"></div>
      <div class="weui_toast">
        <i class="weui_icon_toast"></i>
        <p class="weui_toast_content"></p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        showAlert: false,
        showTip: false,
        error: {
          title: '',
          msg: '',
          status: 200,
          statusText: ''
        }
      }
    },
    events: {
      'response-msg' (response) {
        if (response.status === 401) {
          this.showAlert = true
          localStorage.setItem('jc_user_access_token', '')
        } else if (response.status === 0) {
          this.showAlert = true
          this.error.title = ''
          this.error.msg = ''
          this.error.status = response.status
          this.error.statusText = response.statusText
        } else {
          this.showAlert = true
          this.error.title = response.statusText
          this.error.msg = response.data.error
          this.error.status = response.status
          this.error.statusText = response.statusText
        }
      },
      'show-tip' (response) {
        this.showTip = true
        var self = this
        setTimeout(function () {
          self.showTip = false
        }, 1000)
      }
    }
  }
</script>



